import { Component,ViewChild } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Api } from '../../providers/providers';
import { Nonfin } from '../../providers/providers';
import { NavParams } from 'ionic-angular';
import { Slides } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { AccountPage } from '../account/account';

@Component({
  selector: 'page-dashboard',
  templateUrl: 'dashboard.html'
})

export class DashboardPage {

  @ViewChild(Slides) slides: Slides;

  bankAccount = {};
  bankAccountModel = {};
  loginData = {};
  constructor(
    public navCtrl: NavController,
    public api : Api,
    navParams: NavParams,
    public nonfin : Nonfin,
    public alertCtrl: AlertController ) {
      this.loginData =  navParams.get('loginData');
  }

  showAlert() {
    const alert = this.alertCtrl.create({
      title: 'Information',
      subTitle: 'Sorry, Coming soon.',
      buttons: ['Dismiss']
    });
    alert.present();
  }

  goAccount() {
    // this.navCtrl.push(AccountPage,{loginData:this.loginData});
    this.nonfin.getListAccounts('getListAccounts').then(value => {
      console.log(value);
      this.navCtrl.push(AccountPage,{loginData:this.loginData,listAccount:value});     
    }, reason => {
      console.log(reason);
    })
    

  }

  
  

}



