import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { Api } from '../providers/providers';
import { Nonfin } from '../providers/providers';
import { GlobalVariable } from '../library/globalVariable/globalVariable';
import { HttpModule } from '@angular/http';
import { QRCodeModule } from 'angular2-qrcode';
import { Geolocation } from '@ionic-native/geolocation';


import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { LoginPage } from '../pages/login/login';
import { DashboardPage } from '../pages/dashboard/dashboard';
import { AccountPage } from '../pages/account/account';
import { HeaderPage} from '../pages/header/header';
import { HeaderMenuPage} from '../pages/headerMenu/headerMenu';
import { FooterPage } from '../pages/footer/footer';
import { AccountCodePage } from '../pages/accountCode/accountCode';
import { OurLocationPage } from '../pages/ourLocation/ourLocation';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LoginPage,
    DashboardPage,
    AccountPage,
    HeaderPage,
    FooterPage,
    HeaderMenuPage,
    AccountCodePage,
    OurLocationPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    QRCodeModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LoginPage,
    DashboardPage,
    AccountPage,
    HeaderPage,
    FooterPage,
    HeaderMenuPage,
    AccountCodePage,
    OurLocationPage
  ],
  providers: [
    Api,
    Nonfin,
    StatusBar,
    GlobalVariable,
    HttpModule,
    SplashScreen,
    Geolocation,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



