import { Http } from '@angular/http';
import { Injectable } from '@angular/core';
import { AlertController, LoadingController } from 'ionic-angular';


/**
 * Api is a generic REST Api handler. Set your API url first.
 */
@Injectable()
export class Api {
  ip: string = 'http://10.1.50.118:8765';
  urlLogin: string = this.ip + '/mobile-webconsole/apps/';
  urlTransaction: string =  this.ip + '/mobile-webconsole/apps/1/pbTransactionAdapter/';
  urlNonTransaction: string = this.ip + '/mobile-webconsole/apps/1/pbNonFinancialAdapter/';
  token:string = '';
  metadata = {
    'datetime': '2017-10-06T08:29:47.639Z',
    'deviceId': '',
    'devicePlatform': '',
    'deviceOSVersion': '',
    'deviceType': '',
    'latitude': '',
    'longitude': '',
    'appId': '1',
    'appVersion': '2.0'
  };
 
  
  constructor(public http: Http,
  public alertCtrl: AlertController,
  public loadingController: LoadingController) {
  }
 
  // loading = this.loadingController.create({
  //   content: `
  //     <div class="custom-spinner-container">
  //       <div class="custom-spinner-box"></div>
  //     </div>`,
  // });
  postBase(url : string, endpoint: string, body?: any, reqOpts?: any) {
    return new Promise((resolve, reject) => {
      console.log(body);
      if(body == undefined){
        body = {};
      }
      console.log(body);
      
      let request = {
        'metadata' : this.metadata,
        'body' : body
      }
      reqOpts = {
        'headers' : {
          'x-auth-token' : this.token
        }
       }
      this.loadingShow();
      return this.http.post(url + '/' + endpoint, request, reqOpts).subscribe(
        (data:any)=> {
          let responseWrapper = {
            code : "",
            status : "",
            msg : "",
            body : {}

          }
          let dataa = data.headers;
          this.token =  dataa.get('x-auth-token');
          if(data.status == 200){
            let dataConverted = data.json();
            let bodyConverted = dataConverted['body']; 
            if(bodyConverted.hasOwnProperty("Fault")){
              responseWrapper.msg = bodyConverted.Fault.faultstring.content._text;
              if(responseWrapper.msg != null){
                var codee = this.getErrorCode(responseWrapper.msg); //get 4 digit error code
                var msge = this.errorCodeControl(responseWrapper.msg); //call error code controller
                responseWrapper.msg = msge;
                responseWrapper.code = codee;
                responseWrapper.status = "0";
                //this.loading.dismiss();
                this.errorAlert(responseWrapper.msg);
                return;
              }else{ 
                var codei = this.getErrorCode(responseWrapper.msg); //get 4 digit error code
                var msgi = this.errorCodeControl(responseWrapper.msg); //call error code controller
                responseWrapper.msg = msgi;
                responseWrapper.code = codei;
                responseWrapper.status = "0";
                this.errorAlert(responseWrapper.msg);
               // this.loading.dismiss(); 
                reject (responseWrapper);
                return;
              }
             }else{;
             // this.loading.dismiss();
                resolve (bodyConverted);
             }
          }else{
          }
        },error=> {
          reject (null);
        }
      );
    });
  } 

  getErrorCode(msg){
    var code = msg.substring(0,4);
    if(!Number(code)){
      code = msg;
    }
		return code;
  }

  errorCodeControl(msg){
    var code = this.getErrorCode(msg);
   return code;

 }
  
  errorAlert(msg) {
    const alert = this.alertCtrl.create({
      title: 'Error',
      subTitle: msg,
      buttons: ['Ok']
    });
    alert.present();
  }

  loadingShow(){
  //  this.loading.present();
  }
  
  loadingHide(){
 //   this.loading.dismiss();
  }
  
  postLogin(endpoint: string, body: any, reqOpts?: any) {
    return new Promise((resolve, reject) => {
      let request = {
        'metadata' : this.metadata,
        'body' : body
      }
      this.http.post(this.urlLogin + '/' + endpoint, request, reqOpts).subscribe(
        (data:any)=> {
          let dataa = data.headers;
          this.token =  dataa.get('x-auth-token');
            
          if(data.status == 200){
            let contentResponse = data.json();
            let bodyJ = contentResponse.body;
            let code = contentResponse.status['code'];
            if(code == '200'){
              resolve (bodyJ);
            }else{
              alert(contentResponse.status['description']);
              reject (data.status);
            }
          }else{
            reject (data.status);
          }
        },error=> {
          reject (null);
        }
      )
    });

  }

  postTransaction(endpoint: string, body?: any, reqOpts?: any) {
    let request = {
      'metadata' : this.metadata,
       'body' : body
     }
     reqOpts = {
      'headers' : {
        'x-auth-token' : this.token
      }
     }
     return this.http.post(this.urlTransaction + '/' + endpoint, request, reqOpts)
     .subscribe(
         data=> {
         }
     );
   }

   postNonTransaction(endpoint: string, body?: any, reqOpts?: any) {
    return new Promise((resolve, reject) => {
      if(body == undefined){
        body = {};
      }
      
      let request = {
        'metadata' : this.metadata,
        'body' : body
      }
      reqOpts = {
        'headers' : {
          'x-auth-token' : this.token
        }
       }
      return this.http.post(this.urlNonTransaction + '/' + endpoint, request, reqOpts).subscribe(
        (data:any)=> {
          let dataa = data.headers;
          this.token =  dataa.get('x-auth-token');
          
          if(data.status == 200){
            let contentResponse = data.json();
            let bodyJ = contentResponse.body;
            let code = contentResponse.status['code'];
            if(code == '200'){
              resolve (bodyJ);
            }else{
              alert(contentResponse.status['description']);
              reject (null);
            }
          }else{
          }
        },error=> {
          reject (null);
        }
      );
    });
   }

  put(endpoint: string, body: any, reqOpts?: any) {
    return this.http.put(this.urlLogin + '/' + endpoint, body, reqOpts);
  }

  delete(endpoint: string, reqOpts?: any) {
    return this.http.delete(this.urlLogin + '/' + endpoint, reqOpts);
  }

  patch(endpoint: string, body: any, reqOpts?: any) {
    return this.http.put(this.urlLogin + '/' + endpoint, body, reqOpts);
  }
}



