import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Api } from '../../providers/providers';
import { DashboardPage } from '../dashboard/dashboard';
import { Nonfin } from '../../providers/providers';
import { FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'page-login',
  templateUrl: 'login.html'
})

export class LoginPage {
  loginForm: any;
  loginModel = {};

  constructor(
    public navCtrl: NavController,
    public api : Api,
    public nonfin : Nonfin,
    public formBuilder: FormBuilder) {
      this.loginForm = this.formBuilder.group({
          'username': ['', [Validators.required, Validators.minLength(4)]],
          'password': ['', [Validators.required, Validators.minLength(5)]]
        });
  };

  doLogin(form, loginModel){
    if (this.loginForm.dirty && this.loginForm.valid) {
      console.log(loginModel);
      let postParams = {
          'username': loginModel.username,
          'password': loginModel.password,
          'channel': 'MOB',
          'latitude': '-6.2596093',
          'longitude': '106.663553'
      }

      this.nonfin.login('pocket/login', postParams).then(value => {
        this.navCtrl.push(DashboardPage,{loginData:value});
      }, reason => {
        console.log(reason);

        return false;
      })
    }
    
      }
    
  testInvoke(){
        this.api.postNonTransaction('getCompanyList');
  }
    

}



