import { Component } from '@angular/core';
import { NavParams, NavController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { AccountCodePage } from '../accountCode/accountCode';

@Component({
  selector: 'page-account',
  templateUrl: 'account.html'
})

export class AccountPage {

  loginData = {};
  listAccount = {};
  customer = {};
  bankAccounts = [];
  constructor(
    navParams: NavParams,
    public navCtrl: NavController,
    public alertCtrl: AlertController ) {
      this.loginData =  navParams.get('loginData');
      this.customer = this.loginData['customer'];
      this.listAccount = navParams.get('listAccount');
      this.bankAccounts = this.listAccount['bankAccount'];
  }

  toQR(){
    console.log(this.customer);
    this.navCtrl.push(AccountCodePage,{loginData:this.customer});  
  }

}



